import { OPTIONS_PANE_PROPORTION } from '../constants';
import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import VizOptions from './VizOptions.js';

function style(props) {
  return Object.assign({
    'backgroundColor': 'red',
    'position': 'absolute',
  }, {
    'width': props.width ? props.width * (1 - OPTIONS_PANE_PROPORTION) : 0,
    'height': props.height || 0,
    'left': props.width ? props.width * OPTIONS_PANE_PROPORTION : 0,
  });
}

class Viz extends React.Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    this.props.onWindowUpdate();
    window.addEventListener(
      'resize', this.props.onWindowUpdate);
    window.addEventListener(
      'resize', this.props.onUpdateStructure);
  }

  componentWillUnmount() {
    window.removeEventListener(
      'resize', this.props.onWindowUpdate);
  }

  render() {
    return (
      <div>
        <div style={style(this.props)}>
          dfdgf
        </div>
        <div>
          <VizOptions/>
        </div>
      </div>
    )
  }
}

Viz.propTypes = {
  hello: PropTypes.string,
  onWindowUpdate: PropTypes.func.isRequired,
  onUpdateStructure: PropTypes.func.isRequired,
};

Viz.defaultProps = {
  hello: 'arse',
};

const mapStateToProps = ({ store }) => ({
  ...store,
});

const mapDispatchToProps = dispatch => ({
  onWindowUpdate: () => dispatch({
    type: 'WINDOW_UPDATE',
  }),
  onUpdateStructure: () => dispatch({
    type: 'UPDATE_STRUCTURE',
    payload: { direction: -1 },
  })
});

export default connect(
  mapStateToProps, mapDispatchToProps)(Viz);



